<template>
  <div class="guides-checkbox-wrap">
    <label for class="bko-checkbox" @click.prevent="onClick">
      <input type="checkbox" :checked="isChecked" />
      <span class="checkmark"></span>
      <span>{{ upperCase }}</span>
    </label>
  </div>
</template>

<script>
export default {
  name: "GuideCheckbox",
  props: {
    name: {
      type: String
    },
    slug: {
      type: String
    },
    id: {
      type: [String, Number]
    }
  },

  computed: {
    isChecked() {
      return this.$store.getters.categoryIds.indexOf(this.slug) >= 0;
    },

    upperCase() {
      return this.name[0].toUpperCase() + this.name.slice(1);
    }

    // slug() {
    //   return this.name.toLowerCase().replace(" ", "");
    // }
  },

  methods: {
    onClick() {
      this.$store.commit("changeCategoryIds", this.slug);
    }
  }
};
</script>
