<template>
  <div id="guide-filters" v-show="showFilters" v-if="categories.length">
    <div class="filter-field">
      <div class="close" @click="$store.commit('toggleFilters')">CLOSE</div>
    </div>
    <div class="filter-field">
      <b-dropdown aria-role="limit" v-model="showPerPage">
        <button class="button is-dark" slot="trigger">
          <span>Show per page: {{ showPerPage }}</span>
        </button>
        <b-dropdown-item :value="limit" v-for="(limit, idx) in limits" :key="idx">
          {{
          limit
          }}
        </b-dropdown-item>
      </b-dropdown>
    </div>
    <div class="filter-field">
      <div class="filter-options">
        <div class="button-left">
          <button
            :class="['bko-button', { 'is-active': $store.getters.mode !== '+'}]"
            @click.prevent="$store.commit('changeMode', ',')"
          >
            <span>EASE</span>
          </button>
        </div>
        <div class="button-right">
          <button
            :class="['bko-button', { 'is-active': $store.getters.mode !== ','}]"
            @click.prevent="$store.commit('changeMode', '+')"
          >
            <span>STRICT</span>
          </button>
        </div>
      </div>
    </div>
    <div class="filter-field">
      <div class="button bko-button" @click.prevent="$store.commit('resetFilters')">
        <span>REST FILTERS</span>
      </div>
    </div>
    <div class="filter-field" v-for="(parent, idx) in categories" :key="idx">
      <div class="guides-category-menu" v-if="parent.children.length">
        <div class="guides-category-head">
          <span>FILTER BY {{ parent.name.toUpperCase() }}</span>
        </div>
        <div class="guides-category-content">
          <guide-checkbox
            v-for="(child, cidx) in parent.children"
            :name="child.name"
            :id="child.id"
            :key="cidx"
            :slug="child.slug"
          />
        </div>
      </div>
      <div class="guides-category-menu" v-else>
        <div class="guides-category-content">
          <guide-block :name="parent.name" :id="parent.id" :slug="parent.slug" :key="idx"></guide-block>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import GuideCheckbox from "./Checkbox";

export default {
  name: "GuideFilters",
  components: { GuideCheckbox },
  data() {
    return {
      category_id: null,
      parent_category_id: null,
      categories: null,
      limits: [10, 20, 30]
    };
  },

  created() {
    this.categories = this.walk(
      blackout.categories,
      parseInt(blackout.parent, 10)
    );

    this.parent_category_id = parseInt(blackout.parent, 10);
    this.category_id = parseInt(blackout.category_id, 10);
  },

  computed: {
    showPerPage: {
      get() {
        return this.$store.getters.showPerPage;
      },
      set(val) {
        this.$store.commit("changeShowPerPage", val);
      }
    },
    showFilters() {
      return this.$store.getters.showFilters;
    }
  },

  methods: {
    walk(arr, parent) {
      let results = [];
      arr.forEach(item => {
        if (item.parent === parent) {
          let _item = {
            id: item.term_id,
            parent: item.parent,
            slug: item.slug,
            name: item.name
          };
          let children = this.walk(arr, _item.id);

          if (children.length) {
            _item.children = children;
          }

          results.push(_item);
        }
      });

      return results;
    },
    onCheckboxChange(id) {
      console.log(id);
      const idx = this.categoryIds.indexOf(id);

      if (idx === -1) {
        this.categoryIds.push(id);
      } else {
        this.categoryIds.splice(idx, 1);
      }
    }
  },
  watch: {
    showFilters: () =>
      document.documentElement.classList.toggle(
        "no-scroll-on-filter-mobile-open"
      )
  }
};
</script>