import Vue from "vue";
import Vuex from "vuex";
import qs from "qs";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    showFilters: false,
    current_page: 1,
    total: 0,
    categoryIds: [],
    showPerPage: 10,
    posts: [],
    parent: parseInt(blackout.parent, 10),
    mode: ","
  },
  getters: {
    showFilters: state => state.showFilters,
    current_page: state => state.current_page,
    categoryIds: state => state.categoryIds,
    total: state => state.total,
    showPerPage: state => state.showPerPage,
    posts: state => state.posts,
    mode: state => state.mode
  },
  mutations: {
    changeCategoryIds(state, id) {
      const idx = state.categoryIds.indexOf(id);

      if (idx === -1) {
        state.categoryIds.push(id);
      } else {
        state.categoryIds.splice(idx, 1);
      }
    },
    changePage(state, page) {
      state.current_page = page;
    },
    changeShowPerPage(state, perPage) {
      state.showPerPage = perPage;
    },
    resetFilters(state) {
      state.categoryIds = [];
    },
    changeMode(state, mode) {
      state.mode = mode;
    },
    setPosts(state, posts) {
      state.posts = posts;
    },
    setTotal(state, total) {
      state.total = total;
    },
    toggleFilters(state) {
      state.showFilters = !state.showFilters;
    }
  },
  actions: {
    async fetch_posts({ state, commit }) {
      try {
        const q = qs.stringify({
          action: "blackout_fetch_posts",
          posts_per_page: state.showPerPage,
          mode: state.mode,
          categories: state.categoryIds,
          page: state.current_page,
          parent: state.parent,
          query_vars: blackout.query_vars
        });
        const response = await fetch(`${blackout.ajaxurl}?${q}`, {
          method: "GET"
        });
        const { found_posts, posts } = await response.json();
        console.log(posts);
        commit("setTotal", parseInt(found_posts, 10));
        commit("setPosts", posts);
      } catch (err) {
        console.log(err);
      }
    }
  }
});
