<template>
  <main id="guides">
    <div class="container">
      <div id="guide-columns" class="columns">
        <div :class="rootClasses">
          <guide-list />
        </div>
        <div class="column is-narrow-desktop">
          <guide-filters />
        </div>
      </div>
    </div>
  </main>
</template>

<script>
import GuideList from "./components/GuideList.vue";
import GuideFilters from "./components/GuideFilters.vue";

export default {
  name: "app",
  components: {
    GuideList,
    GuideFilters
  },
  computed: {
    showFilters() {
      return this.$store.getters.showFilters;
    },
    rootClasses() {
      return [
        "column",
        {
          "is-four-fifths-desktop": this.showFilters,
          "is-full": !this.showFilters
        }
      ];
    }
  }
};
</script>
