<template>
  <div id="guide-list">
    <div class="columns is-multiline">
      <div class="column is-full">
        <div class="level">
          <div class="level-left">
            <div class="level-item">
              <b-pagination
                :total="$store.getters.total"
                :per-page="$store.getters.showPerPage"
                :current.sync="current"
                :rangeBefore="rangeBefore"
                :rangeAfter="rangeAfter"
                :size="size"
                :order="order"
                :isSimple="isSimple"
                :isRound="isRound"
              ></b-pagination>
            </div>
          </div>
          <div class="level-right">
            <div class="level-item buttons" @click="$store.commit('toggleFilters')">
              <button class="button is-dark is-small">{{show}}</button>
            </div>
          </div>
        </div>
      </div>
      <guide-block v-for="(post, idx) in posts" :key="idx" :post="post"></guide-block>
    </div>
  </div>
</template>

<script>
import GuideBlock from "./GuideBlock.vue";
import { mapGetters } from "vuex";
export default {
  name: "GuideList",
  components: { GuideBlock },

  data() {
    return {
      showFilters: true,
      max_num_pages: 1,
      rangeBefore: 3,
      rangeAfter: 1,
      order: "",
      size: "is-small",
      isSimple: false,
      isRound: false,
      prevIcon: "chevron-left",
      nextIcon: "chevron-right"
    };
  },

  computed: {
    ...mapGetters(["showPerPage", "mode", "categoryIds", "posts"]),
    show() {
      return this.$store.getters.showFilters ? "Hide Filters" : "Show Filters";
    },
    current: {
      get() {
        return this.$store.getters.current_page;
      },
      set(page) {
        this.$store.commit("changePage", page);
      }
    },
    filters() {
      return {
        showPerPage: this.showPerPage,
        mode: this.mode,
        categoryIds: this.categoryIds,
        currentPage: this.current
      };
    }
  },

  created() {
    if (blackout !== undefined) {
      this.max_num_pages = blackout.max_num_pages ? blackout.max_num_pages : 1;
      this.total = blackout.total_found ? blackout.total_found : 1;
    }
    this.$store.dispatch("fetch_posts");
  },

  watch: {
    filters: {
      handler() {
        this.$store.dispatch("fetch_posts");
      },

      deep: true
    }
  }
};
</script>
